(function() {
  'use strict';
  angular.module("interreps").filter("newArrayFilter", function() {
    return function(reps, competitions) {
      _.each(reps, function(rep) {
        return _.each(competitions, function(comp) {
          var index;
          index = _.findIndex(rep.competitions, function(c) {
            return c.name === comp.name;
          });
          if (index === -1) {
            return rep.competitions.push({
              name: comp.name
            });
          }
        });
      });
      return reps;
    };
  });

}).call(this);



(function() {
  angular.module('interreps').controller('AdminScoresController', function($rootScope, $scope, $timeout, $interval, $filter, $state, toastr, FirebaseService, reps, competitions, allCompetitionsArray) {
    'ngInject';
    $scope.reps = reps;
    $scope.competitions = competitions;
    $scope.allCompetitionsArray = allCompetitionsArray;
    $scope.updatedReps = angular.copy($scope.reps);
    $scope.selectedCompetition = {};
    $scope.selectedReps = [];
    $scope.leaderboard = [];
    $scope.filter = {
      genre: 'G',
      competitions: 'T'
    };
    $rootScope.currentState = _.find($rootScope.menu, function(item) {
      return item.state === $state.current.name;
    });
    $scope.methods = {
      init: function() {
        $scope.leaderboard = [];
        $scope.selectedReps = [];
        _.each($scope.reps, function(rep) {
          var index, obj;
          index = _.indexOf($scope.reps, rep);
          obj = {
            position: index + 1,
            rep: {}
          };
          return $scope.leaderboard.push(obj);
        });
        _.map($scope.allCompetitionsArray, function(competition) {
          var matches;
          matches = competition.name.match(/\b(\w)/g);
          return competition.initials = matches.join('');
        });
        return _.map($scope.updatedReps, function(rep) {
          return _.each($scope.allCompetitionsArray, function(comp) {
            var index;
            index = _.findIndex(rep.competitions, function(c) {
              return c.name === comp.name;
            });
            if (index === -1) {
              rep.competitions = rep.competitions || [];
              return rep.competitions.push({
                name: comp.name
              });
            }
          });
        });
      },
      validateReps: function(rep) {
        var comp, isRep;
        $scope.selectedReps = $scope.selectedReps || [];
        _.each($scope.selectedCompetition.leaderboard, function(item) {
          return $scope.selectedReps.push(item.rep);
        });
        comp = _.find(rep.competitions, function(item) {
          return item.name === $scope.selectedCompetition.name;
        });
        isRep = _.find($scope.selectedReps, function(item) {
          return item && item.name === rep.name;
        });
        if (isRep || !comp) {
          return true;
        }
      },
      validateRepsAdded: function(rep) {
        if (rep) {
          return $scope.selectedReps.push(rep);
        }
      },
      validateRepsRemoved: function($event, rep, idx) {
        var index;
        $event.stopPropagation();
        $scope.leaderboard[idx].rep = {};
        index = $scope.selectedReps.indexOf(rep);
        if (index > -1) {
          return $scope.selectedReps.splice(index, 1);
        }
      },
      changeSelectedCompetition: function(competition) {
        competition = JSON.parse(competition);
        $scope.selectedCompetition = competition;
        if (competition && competition.leaderboard) {
          return $scope.leaderboard = competition.leaderboard;
        } else {
          return $scope.methods.init();
        }
      },
      saveLeaderboad: function(competition) {
        competition = JSON.parse(competition);
        FirebaseService.updateLeaderboard(competition.name, $scope.leaderboard);
        return toastr.success('Classificação salva com sucesso');
      },
      genreFilter: function(item) {
        if ($scope.filter.genre === 'G') {
          return item;
        } else {
          return item.genre === $scope.filter.genre;
        }
      }
    };
    $scope.methods.init();
  });

}).call(this);



