(function() {
  angular.module('interreps').controller('AdminRepsController', function($rootScope, $scope, $timeout, $interval, $filter, $state, $uibModal, $uibModalStack, toastr, FirebaseService) {
    'ngInject';
    var storage;
    storage = firebase.storage();
    $scope.rep = {};
    $scope.reps = $scope.$parent.reps;
    $scope.repsSearched = angular.copy($scope.reps);
    $rootScope.currentState = _.find($rootScope.menu, function(item) {
      return item.state === $state.current.name;
    });
    $scope.methods = {
      init: function() {
        _.map($scope.reps, function(rep) {
          var joker;
          storage.ref("logos/" + rep.user + ".jpg").getDownloadURL().then(function(url) {
            return $timeout(function() {
              rep.url = url;
              return rep;
            });
          });
          if (rep.competitions) {
            joker = _.find(rep.competitions, function(comp) {
              return comp.name === 'Coringa';
            });
            if (joker && joker.team) {
              return rep.joker = joker.team[0];
            } else {
              return rep.joker = {
                name: '-'
              };
            }
          } else {
            return rep.joker = {
              name: '-'
            };
          }
        });
        return _.map($scope.repsSearched, function(rep) {
          var joker;
          storage.ref("logos/" + rep.user + ".jpg").getDownloadURL().then(function(url) {
            return $timeout(function() {
              rep.url = url;
              return rep;
            });
          });
          if (rep.competitions) {
            joker = _.find(rep.competitions, function(comp) {
              return comp.name === 'Coringa';
            });
            if (joker && joker.team) {
              return rep.joker = joker.team[0];
            } else {
              return rep.joker = {
                name: '-'
              };
            }
          } else {
            return rep.joker = {
              name: '-'
            };
          }
        });
      },
      getDetails: function(rep) {
        var modalInstance;
        return modalInstance = $uibModal.open({
          animation: true,
          windowTemplateUrl: 'app/admin/reps/details/window-template.html',
          templateUrl: 'app/admin/reps/details/template.html',
          controller: 'AdminRepsDetailsController',
          backdrop: 'no',
          resolve: {
            rep: function() {
              console.log(rep.competitions);
              return rep;
            }
          }
        });
      },
      saveRep: function() {
        if ($scope.reps) {
          $scope.rep.id = $scope.reps.length + 1;
        } else {
          $scope.rep.id = 1;
        }
        FirebaseService.createRep($scope.rep);
        toastr.success('República criada com sucesso');
        return $timeout(function() {
          return $scope.methods.close();
        }, 500);
      },
      createRep: function() {
        var modalInstance;
        modalInstance = $uibModal.open({
          animation: true,
          windowTemplateUrl: 'window-template.html',
          templateUrl: 'create.html',
          controller: 'AdminRepsController',
          backdrop: 'no',
          resolve: {
            reps: function() {
              return $scope.reps;
            }
          }
        });
        return modalInstance.result.then((function(reps) {
          return console.log('closed');
        }), function() {
          return FirebaseService.getAllReps().then(function(result) {
            $scope.reps = result;
            $scope.repsSearched = angular.copy($scope.reps);
            return $scope.methods.init();
          });
        });
      },
      close: function() {
        return $uibModalStack.dismissAll();
      }
    };
    $scope.$watch('search', function(newVal, oldval) {
      if (newVal !== '') {
        $scope.repsSearched = $filter('filter')($scope.reps, {
          name: newVal
        });
      }
      if (newVal === '') {
        return $scope.repsSearched = angular.copy($scope.reps);
      }
    });
    $scope.methods.init();
  });

}).call(this);



