(function() {
  angular.module("interreps").service("FirebaseService", function($http, $q) {
    var auth, database, service;
    database = firebase.database();
    auth = firebase.auth();
    service = {
      getCurrentAuthuser: function() {
        return auth.currentUser;
      },
      getAllUsers: function() {
        return database.ref('users').once('value').then(function(result) {
          return result.val();
        });
      },
      getUserById: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('users').child(index).once('value').then(function(result) {
          return result.val();
        });
      },
      getAllReps: function() {
        return database.ref('reps').once('value').then(function(result) {
          return result.val();
        });
      },
      getAllRepsOrderedByPoints: function() {
        return database.ref('reps').orderByKey('points').then(function(result) {
          console.log(result.val());
          return result.val();
        });
      },
      getRepById: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).once('value').then(function(result) {
          return result.val();
        });
      },
      getRepCompetitions: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child('index').child('competitions').once('value').then(function(result) {
          return result.val();
        });
      },
      updateRepCompetitions: function(id, competitions) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('competitions').set(competitions);
      },
      updateRepParticipants: function(id, participants) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('participants').set(participants);
      },
      updateRepTotalCost: function(id, totalCost) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('totalCost').set(totalCost);
      },
      updateRepPoints: function(id, points) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('points').set(points);
      },
      createRep: function(rep) {
        return database.ref('reps').once('value').then(function(result) {
          var index, ref;
          index = result.val().length;
          ref = database.ref('reps');
          return ref.child('/' + index).set(rep);
        });
      },
      getAllCompetitions: function() {
        return database.ref('competitions').once('value').then(function(result) {
          return result.val();
        });
      },
      getCompetitionsArray: function() {
        return database.ref('allCompetitions').once('value').then(function(result) {
          return result.val();
        });
      },
      updateLeaderboard: function(key, leaderboard) {
        var reps;
        reps = [];
        database.ref('reps').once('value').then(function(result) {
          return reps = result.val();
        });
        database.ref('allCompetitions').once('value').then(function(result) {
          var allCompetitions, compAllIndex, index;
          index = _.findIndex(result.val(), function(item) {
            return item.name === key;
          });
          allCompetitions = result.val();
          compAllIndex = _.findIndex(allCompetitions, function(comp) {
            return comp.name === key;
          });
          return database.ref('allCompetitions').child(index).child('leaderboard').set(leaderboard).then(function(result) {
            return _.each(leaderboard, function(register) {
              var compIndex, i, rep;
              if (register.rep !== {}) {
                i = _.findIndex(reps, function(rep) {
                  return rep.name === register.rep.name;
                });
                rep = _.find(reps, function(rep) {
                  return rep.name === register.rep.name;
                });
                if (rep) {
                  compIndex = _.findIndex(rep.competitions, function(comp) {
                    return comp.name === key;
                  });
                  if (i !== -1 && compIndex !== -1) {
                    database.ref('reps').child(i).child('competitions').child(compIndex).child('position').set(register.position);
                    return database.ref('reps').child(i).child('competitions').child(compIndex).child('points').set(allCompetitions[compAllIndex].points[register.position - 1]);
                  }
                }
              }
            });
          });
        });
        return service.updatePoints();
      },
      getRules: function() {
        return database.ref('rules').once('value').then(function(result) {
          return result.val();
        });
      },
      updateRules: function(rules) {
        return database.ref('rules').set(rules);
      },
      getPrices: function() {
        return database.ref('prices').once('value').then(function(result) {
          return result.val();
        });
      },
      updatePoints: function() {
        return $q.all({
          reps: service.getAllReps(),
          competitions: service.getCompetitionsArray()
        }).then(function(result) {
          var competitions, reps;
          reps = result.reps;
          competitions = result.competitions;
          return _.each(reps, function(rep) {
            rep.points = 0;
            _.each(competitions, function(comp) {
              if (comp.leaderboard) {
                return _.each(comp.leaderboard, function(item) {
                  if (item.rep && rep.name === item.rep.name) {
                    return rep.points += comp.points[item.position - 1];
                  }
                });
              }
            });
            return service.updateRepPoints(rep.id, rep.points);
          });
        });
      }
    };
    return service;
  });

}).call(this);



